<template>
    <div>
      <div class="position">
        <div class="position-warp">
          <span></span>  <span >位置: </span ><span style="cursor: pointer;" @click="$router.push('/')">政企智慧服务平台</span> &nbsp;<span> > </span>&nbsp; <span>科技中小企业信息管理系统</span> &nbsp;<span> > </span>&nbsp; <span class="need-ting" style="color: #555555;" >企业机构</span>
        </div>
      </div>
      <div class="choice">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="行业">
            <div style="display: inline-block">
              <el-cascader
                v-model="industry"
                :options="option"
                @change="handleChange1"></el-cascader>
            </div>
            <div style="display: inline-block;margin-left: 68px">
              <el-form-item label="所在地">
                <el-cascader
                  size="large"
                  :options="options"
                  v-model="selectedOptions"
                  @change="handleChange">
                </el-cascader>
              </el-form-item>
            </div>
          </el-form-item>
          <el-form-item label="注册时间">

            <div style="display: inline-block;width: 130px">
              <el-date-picker
                v-model="time"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
            </div>


             <span style="color: #606266;box-sizing: border-box;margin-left:226px; ">注册资金（万元）</span>
            <div style="display: inline-block;width: 100px">
              <el-input v-model="form.registeredCapitalBegin"></el-input>
            </div>
            <span class="line"></span>
            <div style="display: inline-block;width: 100px;margin-left: 20px">
              <el-input v-model="form.registeredCapitalEnd"></el-input>
            </div>
           <span style="color: #606266;box-sizing: border-box;margin-left:25px;">从业人数（人）</span>
            <div style="display: inline-block;width: 100px">
              <el-input v-model="form.scaleBegin"></el-input>
            </div>
            <span class="line"></span>
            <div style="display: inline-block;width: 100px;margin-left: 20px">
              <el-input v-model="form.scaleEnd"></el-input>
            </div>
          </el-form-item>
          <el-form-item label="企业类型">
            <el-checkbox-group v-model="typeEnterprise">
              <el-checkbox label="不限" name="type"></el-checkbox>
              <el-checkbox label="高新技术企业" name="type"></el-checkbox>
              <el-checkbox label="技术先进型服务企业" name="type"></el-checkbox>
              <el-checkbox label="新产品备案企业" name="type"></el-checkbox>
              <el-checkbox label="其他" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="关键词">
            <div style="width: 139px;display: inline-block">
              <el-input v-model="form.keyWord"></el-input>
            </div>
            <div style="display: inline-block;width: 74px;height: 32px;margin-left: 15px">
              <el-button type="primary" @click="search(1)" size="small" >查询</el-button>

            </div>
            <div style="display: inline-block">
              <el-button size="small" @click="clear">清空</el-button>
            </div>

          </el-form-item>
        </el-form>
      </div>
      <div class="need-info">
        <div class="info-warp">
          <div class="info-title">
            <span>企业展示平台一览表</span>
          </div>
          <div class="release"><span>我有企业</span><span @click="$router.push('/enterpriseCenter')" style="cursor: pointer">立即认证</span></div>
          <div class="info-list" v-if="companyInfo">
            <ul>
                <li v-for="(item,key) in companyInfo"

                    @mouseover="selectStyle (key)  "
                    :class="{'active':isActive[key]}"
                    @mouseout="outStyle(key)"
                    @click="$router.push({path:'/sHome/TCompanyDetail',query:{id:item.id}})"
                    :key="key"
                >
                <img :src="item.logo" alt="" class="company-logo">
                <div class="company-info">
                  <ul>
                    <li><span class="companyInfo-11">{{item.firmName}}</span><span class="companyInfo-12">{{item.country + item.city + item.district}}</span></li>
                    <li><div style="display: inline-block;width: 214px"><span class="companyInfo-21"> 企业类型：</span><span class="companyInfo-22">{{item.typeEnterprise}}</span></div>  <div style="display: inline-block"><span class="companyInfo-23">注册资金：</span><span class="companyInfo-24">{{item.registeredCapital||0}}万</span></div> </li>
                    <li><div style="display: inline-block;width: 214px">
                      <span class="companyInfo-21">注册时间：</span>
                      <span class="companyInfo-22">{{item.recordDate1}}</span>
                    </div>  <div style="display: inline-block"><span class="companyInfo-23">从业人数：</span><span class="companyInfo-24">{{item.scale|| 0}}人</span></div> </li>
                  </ul>
                </div>
              </li>

            </ul>
          </div>
          <div class="foot">
            <el-pagination
              @current-change="handleCurrentChange"
              background
              layout="prev, pager, next"
              :page-size="12"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </div>

    </div>
</template>

<script>
    import {regionData, CodeToText} from 'element-china-area-data'
    export default {
        name: "Torganize",
        data(){
            return {
                selectedOptions: '',
                options: regionData,
                isActive:{
                    0:false,
                    1:false,
                    2:false,
                    3:false,
                    4:false,
                    5:false,
                    6:false,
                    7:false,
                    8:false,
                    9:false
                },
                value1:'',
                industry:[],
                time:'',
                form: {
                    province:'',
                    city:'',
                    district:'',
                    keyWord:'',   //关键字
                    scaleBegin:'',  //从业人数最小值
                    scaleEnd:'', //从业人数最大值
                    registeredCapitalBegin:'',//注册资金最小值
                    registeredCapitalEnd:'',// 注册资金最大值
                    typeEnterprise:'',
                    recordDateBegin:'',
                    recordDateEnd:''
                },
                option:[
                    {
                        value:'农、林、木、鱼业',
                        label: '农、林、木、鱼业',
                    },{
                        value:'采矿业',
                        label: '采矿业',

                    },{
                        value:'制造业',
                        label: '制造业',
                    },
                    {
                        value:'水利环境公共设施管理',
                        label: '水利环境公共设施管理',
                    },
                    {
                        value:'居民服务、修理和其他',
                        label: '居民服务、修理和其他',
                    },
                    {
                        value:'教育',
                        label: '教育',
                    },
                    {
                        value:'卫生和社会工作',
                        label: '卫生和社会工作',
                    },
                    {
                        value:'文化、体育和娱乐业',
                        label: '文化、体育和娱乐业',
                    },
                    {
                        value:'公共管理保障组织',
                        label: '公共管理保障组织',
                    },
                    {
                        value:'国际组织',
                        label: '国际组织',
                    },
                    {
                        value:'电力热力燃气及水生产供应',
                        label: '电力热力燃气及水生产供应',
                    },
                    {
                        value:'建筑业',
                        label: '建筑业',
                    },
                    {
                        value:'批发和零售业',
                        label: '批发和零售业',
                    },
                    {
                        value:'交通运输、仓储和邮政业',
                        label: '交通运输、仓储和邮政业',
                    },
                    {
                        value:'住宿和餐饮业',
                        label: '住宿和餐饮业',
                    },
                    {
                        value:'信息传输软件和信息技术',
                        label: '信息传输软件和信息技术',
                    },
                    {
                        value:'金融业',
                        label: '金融业',
                    },
                    {
                        value:'房地产业',
                        label: '房地产业',
                    },
                    {
                        value:'租赁和商务服务业',
                        label: '租赁和商务服务业',
                    },
                    {
                        value:'科学研究和技术服务业',
                        label: '科学研究和技术服务业',
                    },
                ],
                typeEnterprise:[], //企业类型
                value:'',
                total:1,
                companyInfo:'',
                falg:true
            }
        },
        created(){
                this.getCompanyList(1);


        },
        methods:{
            handleChange(value) {
                var arr = [];
                value.forEach((item, index) => {
                    arr.push(CodeToText[item])
                })
                this.form.province = arr[0];
                this.form.city = arr[1];
                this.form.district = arr[2];
                console.log(this.form)

            },
            selectStyle(i){
                this.isActive[i] = true;
            },
            outStyle(i) {
                this.isActive[i] = false;
            },
            clear(){
                this.typeEnterprise = [];
                this.selectedOptions = '';
                this.industry = [];
                this.time = '';
                //清空form
                console.log(this.form)
                for(let key in this.form){
                    console.log(key)
                    console.log(this.form[key])
                    this.form[key] = '';
                }
                console.log(this.form)

            },
                 //格式化日期：yyyy-MM-dd
                formatDate(date) {
             var myyear = date.getFullYear();
             var mymonth = date.getMonth() + 1;
             var myweekday = date.getDate();

             if (mymonth < 10) {
                 mymonth = "0" + mymonth;
             }
             if (myweekday < 10) {
                 myweekday = "0" + myweekday;
             }
             return (myyear + "-" + mymonth + "-" + myweekday);
         },
             //检索
                 search(val){
                var that = this;
                this.form.industry = this.industry[0];
                console.log(this.time)

                if (this.time){
                    console.log(this.formatDate(this.time[0]))
                    console.log(this.formatDate(this.time[1]))
                    this.form.recordDateBegin = this.formatDate(this.time[0]);
                    this.form.recordDateEnd =  this.formatDate(this.time[1]);
                }
                this.form.typeEnterprise =  this.typeEnterprise.join(',');
                     //判断form为空 未输入条件


                     this.form.pageNum =  val;
                     this.form.pageSize =  10;
                  this.$axios.get('/bussinessCenter/showByPage',{
                    params:this.form
                })
                    .then(res=>{
                        if (res.data.code==0){
                            that.flag = false;
                            that.$message({
                                message: '检索成功',
                                type: 'success'
                            });
                            var companyInfo = res.data.data.list;
                            companyInfo.forEach((item,key)=>{
                                console.log(item)
                                if (item.recordDate){
                                    companyInfo[key].recordDate1 =  item.recordDate.substring(0,10)
                                }else {
                                    companyInfo[key].recordDate1 = ''
                                }
                            })
                            that.companyInfo =  companyInfo
                            that.total = res.data.data.total;
                        }
                        console.log(res)
                    })
            },
            handleChange1(val){
                console.log(val)
            },
            handleCurrentChange(val) {
                if (this.flag){
                    this.getCompanyList(val);
                }else {
                    this.search(val)
                }

            },
            //获取企业列表
            getCompanyList(val){
                var that = this;
                this.$axios.get('/bussinessCenter/showByPage',{
                    params:{
                        pageNum:val,
                        pageSize:10
                    }
                })
                    .then(res=>{
                        if (res.data.code==0){
                            var companyInfo = res.data.data.list;
                            companyInfo.forEach((item,key)=>{
                                console.log(item)
                                if (item.recordDate){
                                    companyInfo[key].recordDate1 =  item.recordDate.substring(0,10)
                                }else {
                                    companyInfo[key].recordDate1 = ''
                                }
                            })
                            that.companyInfo =  companyInfo
                            that.total = res.data.data.total;
                        }
                        console.log(res)
                    })
            }
        }
    }
</script>

<style scoped lang="less">
  .info-list ul li {
    transition: all 0.5s;
  }
  .active {

    box-shadow: 0px 8px 14px 2px rgba(167, 167, 167, 0.22);
  }
  .position {
    height: 40px;
    background-color: #f3f3f3;
    border: solid 1px #efefef;
    .position-warp {
      width: 1209px;
      height: 100%;
      margin: 0 auto;
      line-height: 40px;
      color: #8a8a8a;
      font-size: 12px;
      position: relative;
      span:nth-child(1){
        display: inline-block;
        width: 13px;
        height: 19px;
        background: url("../../assets/position.png");
        background-size: 100% 100%;
        margin-top: 3px;
        position: absolute;
        top: 50%;
        transform: translateY(-71%);
      }
      span:nth-child(2){
        display: inline-block;
        padding-left: 24px;
      }
    }

  }
  .foot {
    width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 41px 46px 76px 46px;
    text-align: right;
  }
  .choice {
    width: 1200px;
    margin: 29px auto;
    background:rgba(247,247,247,1);
    border:1px solid rgba(224,224,224,1);
    box-sizing: border-box;
    padding-top: 16px;
    padding-left: 42px;
  }
  .need-info {
    width: 100%;
    height: 1326px;
    .info-warp {
      width: 1209px;
      height: 82px;
      margin: 0 auto;
      .info-title {
        color: #3a3a3a;
        font-size: 20px;
        text-align: center;
        height: 82px;
        line-height: 82px;
        position: relative;
        font-weight: 600;
        span {
          display: inline-block;
          color: #3a3a3a;
        }
        span:before{
          content: '';
          width: 407px;
          height: 1px;
          background-color: #d2d2d2;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 50px;
        }
        span:after{
          content: '';
          width: 407px;
          height: 1px;
          background-color: #d2d2d2;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 49px;
        }
      }
    }
  }
  .release span:nth-child(1){
    color: #464646;
    font-family: PingFangSC-Semibold;
    font-size: 16px;
    font-stretch: normal;
    font-weight: 600;
    display: inline-block;
    padding-right: 22px;
  }
  .release {
    text-align: center;
    margin-bottom: 32px;
  }

  .release span:nth-child(2){
    display: inline-block;
    width: 212px;
    height: 42px;
    line-height: 42px;
    background-color: #008fff;
    border-radius: 4px;
    text-align: center;
    font-family: PingFangSC-Semibold;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
    color: #ffffff;
  }
  .info-list {
    >ul {
      overflow: hidden;
      >li {
        list-style: none;
        width: 578px;
        height: 182px;
        background-color: #ffffff;
        border: solid 1px #d2d2d2;
        display: inline-block;
        margin-bottom: 33px;
        position: relative;
        box-sizing: border-box;
        padding: 7px 24px 12px 188px;
      }
      >li:nth-child(odd){
        float: left;
      }
      >li:nth-child(even){
        float: right;
      }
    }
  }
  .company-logo {
    position: absolute;
    width: 153px;
    height: 153px;
    top: 50%;
    transform: translateY(-50%);
    left: 12px;
  }
  .company-info {
    ul {
      li {
        list-style: none;
      }
      li:nth-child(1){
        height: 80px;
        line-height: 80px;
        border-bottom: 1px solid  #e3e3e3;
        .companyInfo-11 {
          color: #454545;
          font-size: 16px;
          font-weight: bold;
        }
        .companyInfo-12 {
          float: right;
          color: #a1a1a1;
          font-size: 12px;
          font-weight: bold;
        }
      }
      li:nth-child(2){
        line-height: 40px;

      }
      li:nth-child(3){
        line-height: 40px;
      }
    }
  }
  .companyInfo-21 {
    color: #6c6c6c;
    font-size: 14px;
    font-weight: bold;

  }
  .companyInfo-22 {
    font-size: 14px;
    color: #969696;
  }
  .companyInfo-23 {
    color: #6c6c6c;
    font-size: 14px;
    font-weight: bold;

  }
  .companyInfo-24 {
    font-size: 14px;
    color: #969696;
  }
  .line {
    display: inline-block;
    position: relative;
    width: 1px;
    height: 1px;
  }
  .line:before {
    content: '';
    display: block;
    position: absolute;
    width: 16px;
    height: 1px;
    background-color: #dadada;
    top: -3px;
    transform: translateY(-50%);
    right: -20px;
  }
</style>



